<template>
  <layout-default>
    <div class="page-content">
      <div class="container-fluid">

          <!-- start page title -->
          <div class="row">
              <div class="col-12">
                  <div class="page-title-box d-flex align-items-center justify-content-between">
                      <h4 class="mb-0">معلومات المورد</h4>
                      <div class="page-title-right">
                          
                          <form-modal ref="FormModal" :fetchData="fetchData"/>
                      </div>

                  </div>
              </div>
          </div>
          <!-- end page title -->
          
          <div class="row mb-4" >
            <div class="row" v-if="supplier">
                <div class="card">
                    <div class="card-body">
                        <div class="text-center">
                            <div class="dropdown float-end">
                                <a class="text-body dropdown-toggle font-size-18" href="#" role="button" data-bs-toggle="dropdown" aria-haspopup="true">
                                    <i class="uil uil-ellipsis-v"></i>
                                </a>
                                
                                <div class="dropdown-menu dropdown-menu-end">
                                    <a @click="$refs.FormModal.edit(supplier)" class="dropdown-item" href="javascript:;">
                                      <i class="far fa-edit"></i> تعديل البيانات
                                    </a>
                                    <a @click="$refs.FormModal.destroy(supplier.id)" class="dropdown-item" href="javascript:;">
                                      <i class="far fa-trash-alt"></i> حذف المورد
                                    </a>
                                </div>
                            </div>
                            <div class="clearfix"></div>
                        </div>

                        <div class="text-muted">
                            <div class=" mt-4">
                                <div class="row ">
                                    <p class="mt-4 mb-1 col-md-6 font-size-15"><b style="float: left;"> الأسم : </b>	&nbsp; {{supplier.name}} </p>
                                    <p class="mt-4 mb-1 col-md-6 font-size-15">
                                      <b style="float: left;"> تليفون أول : </b>	&nbsp; {{supplier.phone1}} 
                                    </p>
                                    <p class="mt-4 mb-1 col-md-6 font-size-15">
                                      <b style="float: left;"> تليفون ثانٍ : </b>	&nbsp; {{supplier.phone2}} 
                                    </p>
                                    <p class="mt-4 mb-1 col-md-6 font-size-15" >
                                      <b style="float: left;" v-if="supplier.amount>=0"> رصيد افتتاحي له : </b>
                                      <b style="float: left;" v-else> رصيد افتتاحي عليه : </b>
                                    	&nbsp; {{moneyFormat(supplier.amount,0)}} L.E
                                    </p>
                                    <p class="mt-4 mb-1 col-md-6 font-size-15" >
                                      <b style="float: left;"> تاريخ التسجيل : </b>
                                    	&nbsp; {{supplier.created_at| moment("DD-MM-YYYY")}}
                                    </p>
                                    <p class="mt-4 mb-1 col-md-6 font-size-15">
                                      <b style="float: left;" class="float-start text-danger" v-if="supplier.total_amount>0"> متبقي له : </b>
                                      <b style="float: left;" class="float-start text-primary" v-else> متبقي عليه : </b>
                                      &nbsp;<span>{{(supplier.total_amount>0)?moneyFormat(supplier.total_amount,0)
                                        :moneyFormat(supplier.total_amount*-1,0)}} L.E</span>	 
                                    </p>
                                    <p class="mt-4 mb-1 col-md-12 font-size-15">
                                      <b style="float: left;"> العنوان : </b>	&nbsp; {{supplier.address}} 
                                    </p>
                                    
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="card">
                    <div class="card-body">
                        <div class="row mb-4">
                          <div class="col-md-3">
                            <Datepicker @input="fetchData(1)" placeholder="From" input-class="form-control" v-model="filter.from" format="dd-MM-yyyy"></Datepicker>
                          </div>
                          <div class="col-md-3">
                            <Datepicker @input="fetchData(1)" placeholder="To" input-class="form-control" v-model="filter.to" format="dd-MM-yyyy"></Datepicker>
                          </div>
                          <div class="col-md-2">
                            <a @click="clear" class="btn text-info" href="javascript:;">Clear</a>
                          </div>
                        </div>
                    </div>
                </div>
                <div class="col-md-12">
                  <div class="card me-1" v-if="amounts&&amounts.length">
                      <div class="card-body">
                        <h5 class="font-size-20 mb-4 mt-5 text-center" style="font-weight:600;">كشف حساب المورد</h5>
                        <table id="datatable" class="table table-bordered dt-responsive nowrap" style="border-collapse: collapse; border-spacing: 0; width: 100%;">
                          <thead>
                            <tr>
                                <th>الرصيد</th>
                                <th>دائن</th>
                                <th>مدين</th>
                                <th>البيان</th>
                                <th>التاريخ</th>
                            </tr>
                          </thead>
                          <tbody>
                            <tr v-for="(amount,index) in amounts">
                              <td v-if="index+1==amounts.length" class="text-success" style="font-weight:bold;">{{moneyFormat(amount['amount'],0)}} L.E</td>
                              <td v-else>{{moneyFormat(amount['amount'],0)}} L.E</td>
                              <td>{{(amount['payment'])?moneyFormat(amount['payment'],0)+' L.E':''}} </td>
                              <td>{{(amount['orders'])?moneyFormat(amount['orders'],0)+' L.E':''}} </td>
                              <td v-if="!amount['order_id']">{{amount['type']}}</td>
                              <td v-else>
                                <router-link :to="{name:'supplier_orders_show', params: { order_id: amount['order_id'] }}" tag="a" href="javascript:;">
                                  {{amount['type']}}
                                </router-link>
                              </td>
                              <td>{{amount['date']| moment("DD-MM-YYYY")}}</td>
                            </tr>
                          </tbody>
                        </table>
                      </div>
                  </div>
                  <div class="card ms-1" v-else>
                      <div class="card-body">
                        <h5 class="font-size-20 mb-4 mt-5 text-center" style="font-weight:600;">كشف حساب المورد</h5>
                        <p class="text-center">لا يوجد نتائج</p>
                      </div>
                  </div>
                </div>
            </div>
        </div>
      </div> <!-- container-fluid -->
    </div>
  </layout-default>
</template>  


<script>
import LayoutDefault from '../../layouts/App';
import PaginationNav from '../../components/pagination';
import FormModal from '../../components/modals/supplier/add';
import Datepicker from 'vuejs-datepicker';
import axios from 'axios';

export default {
  components: {LayoutDefault,PaginationNav,FormModal,Datepicker},
  data() {
      return {
        supplier:null,loader:false,
        amounts:null,
        filter:{
          from:null,
          to:null,
        }
      }
    },  
  created() {
    this.fetchData();
  },
  methods: {
    clear() {
      this.filter.from=null
      this.filter.to=null
      this.fetchData(1)
    },
    fetchData(num=1) {
      this.loader=true;
      let data = {params: {page:num,filter:this.filter}};
      axios.get('/api/suppliers/'+this.$route.params.supplier_id,data).then(({data}) => {
          this.supplier=data.success.supplier;
          // this.orders=data.success.orders;
          // this.payments=data.success.payments;
          this.amounts=data.success.amounts;
          // console.log(data.success.orders);
          this.loader=false;
      }).catch((error) => {this.$router.push({ name: "suppliers"})});  
    },
  },
}
</script>